* {
  border: 0;
  margin: 0;
  padding: 0;
  outline: 0;
}

button {
  cursor: pointer;
}

header {
  margin: 0;
  height: 65px;
  width: 100%;
  background: #212121;
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  width: 100%;
}
header .logo {
  margin-left: 25px;
  color: #fff;
  float: left;
  font-family: "Courier New", Courier, monospace;
  vertical-align: middle;
  font-size: 36px;
  line-height: 65px;
}
header button {
  margin-top: 40px;
  margin-right: 5%;
  background: 0;
  color: #01579B;
  font-weight: 100;
  float: right;
  height: 30px;
}
header button i {
  font-size: 40px !important;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  transition: all 0.25s linear;
}
header button i:before {
  content: "";
  z-index: -1;
  border-radius: 50%;
  position: absolute;
  width: 40px;
  height: 40px;
  background: #F5F5F5;
}
header button:hover {
  color: #0277BD;
}
header button:hover i {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  transition: all 0.25s linear;
}

main {
  margin-top: 85px;
}
main article {
  background: #ffffff;
  margin: 40px 0;
  border-radius: 4px;
  position: relative;
}
main article:last-of-type {
  margin-bottom: 40px;
}
main article .article-wrap {
  height: calc(100% - 20px);
  padding: 20px 10px;
  cursor: pointer;
  background: #ffffff;
  transition: all 0.3s;
  box-shadow: 0 2px 1px -1px rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 1px 3px 0 rgba(0, 0, 0, 0.12);
}
main article .article-wrap:hover {
  background: #E1F5FE;
}
main article .article-wrap:active {
  background: #BBDEFB;
}
main article .article-wrap .head {
  font-size: 36px;
  font-weight: 500;
  color: #212121;
}
main article .article-wrap .brief {
  margin-top: 10px;
  color: rgba(0, 0, 0, 0.54);
  font-size: 20px;
}
main article .article-wrap .comments-count {
  position: absolute;
  color: #01579B;
  font-size: 20px;
  top: 15px;
  right: 20px;
  font-weight: 500;
  line-height: 24px;
}
main article .article-wrap .comments-count i {
  position: relative;
  top: 5px;
}

.spinner {
  margin: 40px auto;
  width: 70px;
  text-align: center;
}

.spinner > div {
  width: 18px;
  height: 18px;
  background-color: #01579B;
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
          animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
          animation-delay: -0.32s;
}

.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
          animation-delay: -0.16s;
}

@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

